<template>
	<div id="projectdet-page">
		<div
			class="projectdet-thumb"
			:style="{ backgroundImage: 'url('+require('../assets/img/project/'+project_item.thumb)+')' }">	
		</div>
		<div class="projectdet container">
			<h3>{{project_item.title}}</h3>
			<p>{{project_item.desc}}</p>
			<p><a :href="project_item.link" class="text-white" title="View in project page" target="_blank"><i class="fas fa-paper-plane mr-2"></i> View Project</a></p>
		</div>
	</div>
</template>

<script>
import '../assets/css/projectdet.css'
import prj_data from '../data/project.json'
export default {
	data(){
		return {
			project_item: {}
		}
	},
	methods:{
		goBack(){
			window.history.back()
		}
	},
	mounted(){
		let slug = this.$route.params.slug
		let project_item
		prj_data.forEach(function(e){
			if(slug == e.slug){
				project_item = e
			}
		})
		this.project_item = project_item
	}
}
</script>